<script>
  import bannerScroll from '../common/js/bannerScroll.js';
  import sort from '../common/js/sort.js';
  export default {
    data() {
      return {
        imageItems: [
          {"urlLink": "", "bannerImage": require("../common/images/banner/1.jpg")},
          {"urlLink": "", "bannerImage": require("../common/images/banner/2.jpg")},
