<style scoped>
/*容器的属性*/
/*
flex-direction // 主轴方向 flex-direction: row | row-reverse | column | column-reverse;
flex-wrap // nowrap | wrap | wrap-reverse;
flex-flow // 是flex-direction属性和flex-wrap属性的简写形式，默认值为row nowrap。
justify-content // 属性定义了项目在主轴上的对齐方式。  flex-start | flex-end | center | space-between | space-around;
align-items   // 属性定义项目在交叉轴上如何对齐。 align-items: flex-start | flex-end | center | baseline | stretch;
align-content // 属性定义了多根轴线的对齐方式。如果项目只有一根轴线，该属性不起作用。 flex-start | flex-end | center | space-between | space-around | stretch;
*/


/*以下6个属性设置在项目上。*/
/*
order // 属性定义项目的排列顺序。数值越小，排列越靠前，默认为0。
flex-grow // 属性定义项目的放大比例，默认为0，即如果存在剩余空间，也不放大
flex-shrink // 属性定义了项目的缩小比例，默认为1，即如果空间不足，该项目将缩小
flex-basis // 属性定义了在分配多余空间之前，项目占据的主轴空间（main size）。
flex 属性是flex-grow, flex-shrink 和 flex-basis的简写，默认值为0 1 auto。后两个属性可选。
align-self 属性允许单个项目有与其他项目不一样的对齐方式，可覆盖align-items属性。默认值为auto，表示继承父元素的align-items属性，如果没有父元素，则等同于stretch。
*/
.page-contain {
    position: relative;
    width: 100%;
    height: 100%;
}

.page-contain-box {
  min-height: 100%;
  background: #f4f4f4;
}

.page-contain-banner {
    height: 3.2rem;
}

.page-banner-box {
  position: relative;
  width: 100%;
  height:100%;
  overflow: hidden;
}

.page-banner-img {
  position: absolute;
  z-index: 99;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition-duration: 0ms;
  background: #444;
}

.page-banner-img:nth-child(1) {
  transform: translate(0px, 0px) translateZ(0px);
}

.page-banner-img:nth-child(2) {
  transform: translate(-375px, 0px) translateZ(0px);
}

.page-banner-img:nth-child(3) {
  transform: translate(-375px, 0px) translateZ(0px);
}

.page-banner-img:nth-child(4) {
  transform: translate(-375px, 0px) translateZ(0px);
}

.page-banner-img:nth-child(5) {
  transform: translate(-375px, 0px) translateZ(0px);
}

.page-banner-slide-item {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.page-banner-slide-item img{
  width: 100%;
  height: 100%;
}

.page-contain-homer {
  position: relative;
}

.page-li-box {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-direction: row;
    overflow: hidden;
    align-content: space-between;
    height: 160px;
    width: 100%;
}

.page-li {
  display: flex;
  margin-left: 0.32rem;
  overflow: hidden;
  width: 61px;
  height: 67px;
  background: #d6d7dc;
  text-align: center;
  line-height: 67px;
  -webkit-box-orient: vertical;
  flex-direction: column;
}

.page-li-img {
  display: flex;
  width: 61px;
  height: 48px;
  background: #323232;
}

.page-li-title {
  display: inline-block;
  overflow: hidden;
  font-size: 11px;
  margin-top: 5px;
  height: auto;
  text-align: center;
  color: rgb(102, 102, 102);
  width: 61px;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 14px;
  color: #000;
}

.page-contain-new {
  position: relative;
  display: flex;
  width: 100%;
  height:60px;
  background: #333;
  overflow: hidden;
}

.page-new-title {
  position: absolute;
  width: 35px;
  height:35px;
  background: red;
  z-index: 400;
  margin-top: 12.5px;
  margin-left: 12px;
  display: flex;
}

.page-new-container {
  position: absolute;
  width: 100%;
  background: #666;
  padding-left: 58px;
  left: 0px;
  right: 0px;
  height:100%;
  overflow: hidden;
  display: flex;
}
</style>


